"use client";
import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import Bold from "@tiptap/extension-bold";
import Italic from "@tiptap/extension-italic";
import Underline from "@tiptap/extension-underline";
import Link from "@tiptap/extension-link";
import BulletList from "@tiptap/extension-bullet-list";
import OrderedList from "@tiptap/extension-ordered-list";
import ListItem from "@tiptap/extension-list-item";
import Subscript from "@tiptap/extension-subscript";
import Superscript from "@tiptap/extension-superscript";
import { useEffect, useState } from "react";
import katex from "katex";
import "katex/dist/contrib/mhchem.min.js";
import * as mammoth from "mammoth";

export default function ChemicalEquationConverter() {
  const [originalText, setOriginalText] = useState("");
  const [latexOutput, setLatexOutput] = useState("");
  const [fileName, setFileName] = useState("");

  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    setFileName(file.name);

    try {
      // Read DOCX content
      const arrayBuffer = await file.arrayBuffer();
      const result = await mammoth.extractRawText({ arrayBuffer });
      const text = result.value;

      setOriginalText(text);
      convertToLatex(text);
    } catch (error) {
      console.error("Error reading file:", error);
    }
  };

  const convertToLatex = (text) => {
    // Step 1: Clean and normalize the text
    let cleaned = text
      .replace(/〖〗/g, "")
      .replace(/\u00A0/g, " ")
      .trim();

    // Step 2: Convert Word-style arrows to mhchem syntax
    cleaned = cleaned
      .replace(/→┴\(([^)]+)\)/g, " ->[$1]")
      .replace(/→┬\(([^)]+)\)/g, " ->[][$1]")
      .replace(/→┬\(([^)]+)\)┴\(([^)]+)\)/g, " ->[$1][$2]")
      .replace(/→/g, " -> ");

    // Step 3: Fix chemical formulas
    cleaned = cleaned
      .replace(/([A-Za-z])_([0-9])/g, "$1$2") // CH_3 → CH3
      .replace(/\(([^)]+)\)_([0-9])/g, "($1)$2") // (CH3)_2 → (CH3)2
      .replace(/"([^"]+)"/g, '"$1"'); // Preserve quotes

    // Step 4: Render with KaTeX
    try {
      const latex = `\\ce{${cleaned}}`;
      const rendered = katex.renderToString(latex, {
        throwOnError: false,
        displayMode: true,
      });
      setLatexOutput(rendered);
    } catch (error) {
      console.error("LaTeX rendering error:", error);
      setLatexOutput("Error converting to LaTeX");
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Chemical Equation Converter</h1>
      
      <div className="mb-6">
        <label className="block mb-2 font-medium">
          Upload Word DOCX File:
        </label>
        <input
          type="file"
          accept=".docx"
          onChange={handleFileUpload}
          className="block w-full text-sm text-gray-500
            file:mr-4 file:py-2 file:px-4
            file:rounded-md file:border-0
            file:text-sm file:font-semibold
            file:bg-blue-50 file:text-blue-700
            hover:file:bg-blue-100"
        />
        {fileName && (
          <p className="mt-2 text-sm text-gray-600">
            Uploaded: {fileName}
          </p>
        )}
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="border rounded-lg p-4">
          <h2 className="text-xl font-semibold mb-2">Original Content</h2>
          <div className="p-3 bg-gray-50 rounded min-h-32 whitespace-pre-wrap">
            {originalText || "No content yet"}
          </div>
        </div>

        <div className="border rounded-lg p-4">
          <h2 className="text-xl font-semibold mb-2">LaTeX Output</h2>
          <div
            className="p-3 bg-gray-50 rounded min-h-32"
            dangerouslySetInnerHTML={{
              __html: latexOutput || "Converted output will appear here",
            }}
          />
          {latexOutput && (
            <div className="mt-4 p-3 bg-blue-50 rounded">
              <h3 className="font-medium mb-1">LaTeX Code:</h3>
              <code className="text-sm bg-white p-2 block rounded overflow-x-auto">
                {`\\ce{${originalText
                  .replace(/→┴\(([^)]+)\)/g, " ->[$1]")
                  .replace(/→┬\(([^)]+)\)/g, " ->[][$1]")
                  .replace(/→┬\(([^)]+)\)┴\(([^)]+)\)/g, " ->[$1][$2]")
                  .replace(/→/g, " -> ")
                  .replace(/([A-Za-z])_([0-9])/g, "$1$2")}}`}
              </code>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}